// Journal Entry Detail API Route
// GET/PUT/DELETE /api/v1/accounting/journal-entries/[id]

import { NextRequest, NextResponse } from 'next/server'
import { internalAccountingService } from '@/lib/accounting'

export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ id: string }> }
) {
  try {
    const { id } = await params
    const { searchParams } = new URL(request.url)
    const subAccountId = searchParams.get('subAccountId')

    if (!subAccountId) {
      return NextResponse.json(
        { error: 'subAccountId is required' },
        { status: 400 }
      )
    }

    const entries = internalAccountingService.getJournalEntries(subAccountId)
    const entry = entries.find(e => e.id === id || e.entryNumber === id)

    if (!entry) {
      return NextResponse.json(
        { error: 'Journal entry not found' },
        { status: 404 }
      )
    }

    return NextResponse.json({
      success: true,
      data: { entry },
    })
  } catch (error: any) {
    console.error('Get journal entry error:', error)
    return NextResponse.json(
      { error: error.message || 'Failed to get journal entry' },
      { status: 500 }
    )
  }
}

// POST for actions: post, void
export async function POST(
  request: NextRequest,
  { params }: { params: Promise<{ id: string }> }
) {
  try {
    const { id } = await params
    const body = await request.json()
    const { action, reason } = body

    if (!action) {
      return NextResponse.json(
        { error: 'action is required (post, void)' },
        { status: 400 }
      )
    }

    let entry

    switch (action) {
      case 'post':
        entry = internalAccountingService.postJournalEntry(id)
        break

      case 'void':
        if (!reason) {
          return NextResponse.json(
            { error: 'reason is required for voiding' },
            { status: 400 }
          )
        }
        entry = internalAccountingService.voidJournalEntry(id, reason)
        break

      default:
        return NextResponse.json(
          { error: `Unknown action: ${action}` },
          { status: 400 }
        )
    }

    return NextResponse.json({
      success: true,
      data: { entry },
      message: action === 'post' ? 'Journal entry posted' : 'Journal entry voided',
    })
  } catch (error: any) {
    console.error('Journal entry action error:', error)
    return NextResponse.json(
      { error: error.message || 'Action failed' },
      { status: 500 }
    )
  }
}
